.about {
  height: 100vh;
  align-items: center;
}
.ab{
  height: 625px;
  background-color: rgb(187, 248, 250);
}
.head {
  color: #330c43;
  padding-left: 100px;
  margin-top: 50px;
}

.aboutcontent {
  color: white;
  padding: 2rem;
  display: flex;
  flex-direction: column; 
  height: 100%; 
}

.abouttitle {
  font-size: 3rem;
  margin-bottom: 20px;
}

.aboutpara {
  font-size: 18px;
  line-height: 1.5;
  color: #330c43;
  text-align: justify;
}

.aboutimage {
  max-width: 100%;
  height: auto;
}

.info-point {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.arrow-icon {
  font-size: 20px;
  margin-right: 10px;
  color: #330c43;
}
.abouthire-button {
  margin-top: 20px;
  padding: 8px 16px;
  background-color: rgb(108, 228, 247);
  color: rgba(23,93,105,1);
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: inline-flex;
  align-items: center;
  border-radius: 8px;
  font-size: 16px;
}

.abouthire-button:hover {
  background-color: rgb(1, 218, 252);
  color: rgba(23,93,105,1);
}

@media screen and (max-width: 768px) {
  .aboutcontent {
    height: auto;
    margin-bottom: -200px;
  }
  .ab{
    height: 500px;
  }
.aboutimage{
  height: 350px;
  width: 100%;
}
  .aboutpara {
    font-size: 18px;
    padding-top: -10px;
    text-align: justify;
  }

  .head {
    font-size: 28px;
    padding-left: 0;
    margin-top: -230px;
  }
  .about {
    flex-direction: column;
  }
  .ab{
    height: 1050px;
  }

  .aboutcontent {
    flex-basis: 100%;
    margin-top: -10px;
  }
}
