.homecontact {
  background-color: white; 
  padding: 50px 0;
  min-height: 100vh;
}

.centered-column {
  display: flex;
  align-items: center;
}

.contenthome {
  color: rgba(51,12,67,1);
  max-width: 600px; 
  margin: 0 auto;
}

h1 {
  font-size: 36px;
  margin-bottom: 20px;
}

.additional-text {
  font-size: 18px;
  margin-bottom: 30px;
  text-align: justify;
}

.homehire-button {
  margin-top: 20px;
  padding: 8px 16px;
  background-color: rgb(225, 163, 250);
  color: rgba(51,12,67,1);
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: inline-flex;
  align-items: center;
  border-radius: 8px;
  font-size: 16px;
}

.homehire-button:hover {
  background-color: rgb(154, 71, 187);
  color: rgba(51,12,67,1);
}


.working-image {
  width: 100%;
  height: auto;
}
.tx{
  padding-left: 5px;
}

@media screen and (max-width: 768px) {
  .centered-column {
    flex-direction: column;
    text-align: center;
    padding: 30px;
  }

  .contenthome {
    max-width: 100%;
  }
  .contenthome h1{
   font-size: 30px;
  }

  .homehire-button {
    margin-top: 0;
  }
}
