.aboutheader{
  text-align: center;
  color: rgba(51,12,67,1);
}

.wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
}

.slider {
	height: 120px;
	margin: auto;
	overflow: hidden;
	position: relative;
	width: 80%;
}

.slider .slide-track {
	-webkit-animation: scroll 30s linear infinite;
	animation: scroll 30s linear infinite;
	display: flex;
	width: calc(250px * 10);
}
.slider .slide-track:hover{
  animation-play-state: paused;
}

.slider .slide {
	height: 100px;
	width: 250px;
}
.slider .slide img:hover {
	transform: scale(1.1);
  /* border: 2px solid rgb(71, 70, 70); */
}

.slider .slide img {
  width: 80%;
	height: 100px;
	border-radius: 10px;
	/* border: 2px solid #4e4d4d; */
}

@-webkit-keyframes scroll {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(calc(-250px * 7));
	}
}

@keyframes scroll {
	0% {
		transform: translateX(0);
	}
	100% {
		transform: translateX(calc(-250px * 7));
	}
}