.servicetext{
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
margin-top: -10px;
}

.servicecontent {
  text-align: center;
  justify-content: left;
  height: auto;
  color: white;
  padding: 5rem;
  margin-top: 50px;
}
.servicetitle{
  font-size: 3rem;
}
.servicepara{
  padding-top: 20px;
  font-size: 20px;
  text-align: justify;
}
.service-point {
  display: flex;
  text-align: left;
  margin-bottom: -20px;
}
.servicepara2{
  font-size: 18px;
  padding: 20px;
}
.servicearrow-icon {
  font-size: 18px;
  color: white; 
}
.service-image {
  width: 100%;
  height: auto;
  display: block;
}
.centered-content {
  text-align: center;
}

.additional-text {
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .servicecontent {
    padding: 1rem;
  }
  .servicetitle{
    font-size: 1.5rem;
    padding-top: -50px;
  }
  .servicepara{
    font-size: 16px;
    line-height: 1.5;
    text-align: justify;
  }
  .service-point {
    display: flex;
    text-align: left;
    padding-left: 20px;
  }
}