.homeabout {
  position: relative;
  height: 100vh;
  background-size: cover;
  color: rgba(51,12,67,1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
}

.text{
  background-color: white;
  margin-top: 50px;
}

.homepara2{
  text-align: center;
  margin-bottom: 16px;
  padding-bottom: 5px;
  font-size: 18px;
  max-width: 700px;
}
.about-text {
  font-size: 3rem;
  margin-bottom: 16px;
  padding-top: 20px;
}

.h1 {
  font-size: 32px;
  margin: 16px 0;
}

.custom-buttonab {
  background-color: purple;
  border-color: purple;
  color: white;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
}

.custom-buttonab:hover {
  background-color: white;
  color: purple;
}

@media screen and (max-width: 768px) {
  .text{
    margin-left: 1px;
  }
  .homeabout {
    height: auto;
  }
}