.opening-container {
  background-color: rgb(23, 93, 105); 
  padding: 20px;
  height: 800px;
}

.cheader {
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 20px;
  color: white;
  text-align: center;
}

.job-card {
  transition: transform 0.2s ease-in-out;
  height: 100%;
}

.job-card:hover {
  transform: scale(1.05);
}

.career-image {
  width: 100%;
  height: auto;
}

.careerbutton {
  margin-left: 14px;
  background-color: rgb(23, 93, 105);
  border: none;
  color: white;
  font-size: 16px;
}

.careerbutton:hover {
  background-color: white;
  border: 2px solid rgb(23, 93, 105);
  color: white;
}

.card-career {
  padding: 10px;
  height: auto;
  margin-top: -30px;
  display: flex;
  flex-direction: column;
  text-align: justify;
}

.careertitle {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 8px;
  text-align: center;
}

.careerdescription {
  margin-bottom: -40px;
}

.jobbutton {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  padding-top: -50px;
}

@media (max-width: 768px) {
  .cheader {
    font-size: 32px;
    margin-top: 20px;
    height: auto;
    margin-bottom: -10px;
  }
  .opening-container {
    background-color: rgb(23, 93, 105); 
    padding: 20px;
    height: auto;
    padding-bottom: 50px;
  }
  .careertitle {
    margin-top: -30px;
    text-align: center;
  }
  .card-career {
    height: auto;
    margin-top: 0;
    padding: 0;
    padding-top: 10px;
  }
  .job-card {
    height: auto;
  }
  .jobbutton {
    text-align: center;
  }
  .careerbutton {
    margin-left: 0;
    font-size: 14px;
    margin-bottom: 10px;
  }
  .careerdescription {
    margin-bottom: 40px;
    padding: 5px;
    text-align: left;
  }
}
