.mobile-row {
  background-color: #f0f0f0;
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
  padding: 20px;
  height: 600px;
}


.mobile-container {
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: rgb(255, 198, 245);
  color: rgba(51, 12, 67, 1);
  padding: 30px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.mobile-container:hover {
  color: rgba(51, 12, 67, 1);
  transform: scale(1.02);
  background-color: rgb(255, 95, 207);
}

.mobile-header {
  font-size: 28px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile-text {
  font-size: 16px;
  padding: 20px;
}
.mobile-point {
  display: flex;
  margin-bottom: 15px;
  /* padding-left: 50px; */
}
.mobilepa1{
  font-size: 18px;
  padding: 20px;
  text-align: justify;
}
.mobilepa{
  font-size: 18px;
}

.mobile-icon {
  font-size: 24px;
  margin-left: 10px;
  color: rgba(51, 12, 67, 1); 
}

.mobile-image {
  width: 100%;
  height: auto;
  display: block;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}


.mobile-image:hover {
  transform: scale(1.03);
}

@media (max-width: 768px) {
  .mobile-row {
    height: auto;
  }
  .mobile-icon {
    margin-left: 0;
    /* padding-top: 10px; */
  }
  .mobile-point {
    margin-bottom: 5px;
  }
  .mobile-header {
    font-size: 24px;
    text-align: center;
    margin-bottom: 0;
  }
  .mobilepa{
    /* padding-top: 10px; */
    font-size: 16px;
  }
  .mobilepa1{
    font-size: 16px;
    padding: 5px;
    text-align: justify;
  }
}