.services-container {
  position: relative;
}

.overlay-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  width: 80%; 
  max-width: 600px; 
}

.image-carousel {
  position: relative;
  max-height: 700px; 
}


.image-bg {
  max-width: 100%;
  height: auto; 
}

.service-head {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
  width: 80%;
  max-width: 600px; 
}

@media (max-width: 768px) {
  .image-bg {
    width: 100%;
    height: 500px;
  }
  .overlay-text,
  .service-head {
    width: 90%;
  }
}
