.cards {
  max-width: 1100px;
  margin: 0 auto;
  text-align: center;
  padding: 30px;
}

.cards h2.socialheader {
  font-size: 30px;
  margin: 0 0 30px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: white;
}

.socialbody {
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
}

.services {
  display: flex;
  align-items: center;
}

.social-image {
  width: 100%;
  height: 100%;
  text-align: center;
}

.cardcontent {
  margin: 20px;
}

.cardcontent:hover {
  transform: scale(1.1);
}

.content {
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  margin: 20px;
  padding: 20px;
  border: 2px solid rgba(51, 12, 67, 1);
  border-radius: 20px;
  transition: all 0.3s ease;
}

.content .fab {
  font-size: 40px;
  margin: 16px 0;
  text-align: center;
  padding-left: 40%;
}

.content > * {
  flex: 1 1 100%;
}

.content:hover {
  color: white;
}

.content h2 {
  font-size: 30px;
  margin: 16px 0;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.content p {
  font-size: 17px;
  font-family: 'Poppins', sans-serif;
}

.content a {
  margin: 22px 0;
  background: rgba(51, 12, 67, 1);
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid rgba(51, 12, 67, 1);
  padding: 15px 0;
  border-radius: 25px;
  transition: 0.3s ease;
}

.content a:hover {
  border-radius: 4px;
}

@media (max-width: 900px) {
  .services {
    display: flex;
    flex-direction: column;
  }
  .cardcontent {
    margin: 10px 30px;
  }

  .cardcontent:hover {
    transform: scale(1.1);
  }
}
