.careerheader{
  height: auto;
}
.careerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center; 
}
.careercontent{
  text-align: left;
  flex: 1;
}
.careerhead {
  color: white;
  font-size: 45px;
}

.careerpara {
  font-size: 20px;
  line-height: 1.5;
  color: white;
  padding: 20px;
}


@media screen and (max-width: 768px) {
  .careercontent {
    margin: 20px;
    text-align: justify;
  }

  .careerheader {
    display: flex;
    flex-direction: column; 
    margin-left: 0;
  }

  .careerWrapper {
    order: 2; 
  }

  .careerhead {
    font-size: 24px;
    text-align: center;
    margin-top: 20px;
  }

  .careerpara {
    font-size: 16px;
    margin-right: 0;
    padding: 5px;
  }
}
