.rate{
  margin: 30px;
  margin-left: 20px;
  margin-right: 20px;
}
.rating{
  height: auto;
}
.rate-image{
  height: 150px;
  width: 150px;
}
.aboutheader{
  font-size: 30px;
  padding: 10px;
}
@media screen and (max-width: 768px) {
  .aboutheader{
    font-size: 24px;
    padding: 10px;
  }
  .rate-image{
    height: 150px;
    width: 150px;
  }
  .rate{
    text-align: center;
  }
}