.chat-row {
    padding: 20px;
}

.chat-container {
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: rgb(231, 169, 250);
  color: rgba(51,12,67,1);
  padding: 30px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}
.chat-container:hover {
  color: rgba(51,12,67,1);
  transform: scale(1.02);
  background-color: rgb(216, 90, 255);
}
.chat-header {
  font-size: 28px;
  margin-bottom: 10px;
  align-items: center;
  padding-left: 120px;
}

.chat-text {
  font-size: 18px;
  padding: 20px;
  text-align: justify;
}
.chat-point {
  display: flex;
  margin-bottom: 15px;
}
.chat-image {
  width: 100%;
  height: auto;
  display: block;
}

.chatpa{
  font-size: 18px;

}
.chat-image:hover{
  transform: scale(1.03);
}

@media (max-width: 768px) {
  .chat-container {
    margin: 10px;
    display: flex;
    flex-direction: column;

  }
  .chat-text {
    font-size: 16px;
    padding: 0;
    line-height: 1.5;
    padding-bottom: 10px;
    text-align: justify;
  }
  .chatpa{
    font-size: 14px;
  }
  .chat-header {
    padding-left: 0px;
    font-size: 24px;
    text-align: center;
  }
  .chat-point {
    margin-left: -5px;
  }
}
