.website-row {
  background-color: #f0f0f0;
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
  padding: 20px;
  height: 600px;
}

.website-container {
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: rgb(255, 217, 195);
  color: rgba(51, 12, 67, 1);
  padding: 30px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.website-container:hover {
  color: rgba(51, 12, 67, 1);
  transform: scale(1.02);
  background-color: rgb(248, 148, 90);
}

.website-content {
  /* align-items: center; */
  display: flex;
  flex-direction: column;
}

.website-header {
  font-size: 28px;
  margin-bottom: 5px;
  padding-left: 130px;
}

.website-text {
  font-size: 18px;
  padding: 20px;
  text-align: justify;
}

.website-image {
  width: 100%;
  height: 500px;
  display: block;
  margin-top: 50px;
}


.website-image:hover {
  transform: scale(1.03);
}

@media (max-width: 768px) {
  .website-row {
    height: auto;
  }
  .website-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .website-text{
    padding: 5px;
    font-size: 16px;
    line-height: 1.5;
    text-align: justify;
  }
  .website-header {
    padding-left: 0;
    text-align: center;
    font-size: 24px;
  }
  .website-image {
    height: 300px;
  }
}