.careerbody {
  /* background-image: url('../../../images/formbg3.jpg'); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgb(76, 153, 241);
  /* width: 100vw;  */
  height: 100vh; 
  display: flex;
  justify-content: center;
  align-items: center;
}



.career-container {
  margin: 50px;
  width: 85%;
  display: flex;
  align-items: stretch;
  justify-content: center;
  border-radius: 50px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.image-box {
  flex: 1;
  overflow: hidden;
  border-radius: 50px;
}

.image-box img {
  width: 100%;
  height: auto;
  display: block;
}

form {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 60px;
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
  color: white;
  width: 100%;
}

.careertopic {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: white;
}


.custom-submit-button {
  margin-left: 14px;
  background-color: rgb(76, 153, 241);
  border: none;
  color: white;
  font-size: 16px;
}

.custom-submit-button:hover {
  background-color: white;
  border: 2px solid rgb(76, 153, 241);
  color: white;
}

.careerbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  padding: 50px;
  border: 1px solid #ccc;
  border-radius: 0 50px 50px 0;

}

.custom-input,
.custom-textarea {
  width: 100%;
  margin-bottom: 5px;
  font-size: 20px;
}
.custom-upload-input{
  width: 100%;
  margin-bottom: 5px;
  font-size: 18px;
}
.custom-textarea .ant-input {
  padding: 0;
}

@media (max-width: 768px) {
  .careerbody{
    height: auto;
  }
  .career-container {
    margin: 10px;
    flex-direction: column;
  }
  .careerbox{
    width: 100%;
    border-radius: 0 0 50px 50px;
    padding: 10px;
  }
  .image-box {
    margin-right: 0;
  }
  .careertopic {
    font-size: 24px;
    text-align: center;
  }

  .image-box img {
    order: -1;
  }
}
