.cardshome{
  max-width: 1100px;
  margin: 0 auto;
  text-align: center;
  padding: 30px;
}
.cardshome h2.socialheader{
  font-size: 30px;
  margin: 0 0 30px 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: white;
}
.socialbody{
  background: rgb(23,93,105);
  background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%); 
}
.cardcontent1{
  margin: 20px;
  height: 180px;
  padding-left: 20px;
  padding-right: 20px;
  width: 251px;
}

.cardcontent1:hover {
  transform: scale(1.03); 
}
.content h2{
  font-size: 30px;
  margin: 16px 0;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.content p{
  font-size: 17px;
  font-family: 'Poppins',sans-serif;
}
.content a{
  margin: 22px 0;
  background: rgba(51,12,67,1);
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  border: 1px solid rgba(51,12,67,1);
  padding: 15px 0;
  border-radius: 25px;
  transition: .3s ease;
}
.content a:hover{
  border-radius: 4px;
}
@media (max-width: 900px) {
  .services{
    display: flex;
    flex-direction: column;

  }
  .row1{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .cardcontent1{
    width: auto;
    height: auto;
    margin-right: 30px;
    padding: 2px;
  }
  
  .cardcontent1:hover {
    transform: scale(1.1); 
  }
}