.home{
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
}

.content1 {
  text-align: center;
  justify-content: left;
  height: auto;
  color: white;
  padding: 5rem;
  margin-top: 5rem;
}
.hometitle{
  font-size: 2.7rem;
}
.homepara{
  padding-top: 40px;
  font-size: 24px;
}

.left-image,
.working-image {
  width: 100%;
  height: auto;
  display: block;
}

.centered-content {
  text-align: center;
}

.additional-text {
  font-size: 18px;
}

@media screen and (max-width: 768px) {
  .content1 {
    padding: 2rem;
  }
  .hometitle{
    font-size: 1.5rem;
    margin-top: -100px;
    padding-bottom: 20px;
  }
  .homepara{
    font-size: 15px;
  }
}