.homeinfo {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: white;
}

.hcentered-column1 {
  padding-left: 20px;
  text-align: center;
}
.app{
  font-size: 40px;
}
.hinfo-point {
  display: flex;
  text-align: center;
  margin-bottom: 15px;
  padding-left: 50px;
}

.homepa{
  font-size: 18px;
}
.homepara{
  font-size: 18px;
  padding: 20px;
  margin-top: -20px;
}
.harrow-icon {
  font-size: 18px;
  margin-right: 10px;
  color: white; 
}

.hcareer-button {
  padding: 8px 16px;
  background-color: white;
  color: purple;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: inline-flex;
  align-items: center;
  border-radius: 8px;
}

.hcareer-button:hover {
  background-color: purple;
  color: white;
}

@media screen and (max-width: 768px) {
  .homeinfo{
    margin-bottom: 50px;
    height: auto;
  }
  .app{
    text-align: center;
  }
  .hcentered-column1 {
    text-align: left;
    padding-left: 20px;
  }
  .homepa{
    font-size: 16px;
  }
  .app{
    font-size: 24px;
    padding-right: 20px;
  }
  .harrow-icon {
    font-size: 18px;
    margin-right: 10px;
    color: white; 
  }
  .hinfo-point {
    display: flex;
    text-align: center;
    margin-bottom: 15px;
    margin-top: 10px;
    padding-left: 15px;
  }
  .homepara{
    padding: 10px;
    padding-left: -40px;
  }
  .hcareer-button {
    margin-left: 15px;
  }
}
