.address{
    background: rgb(23,93,105);
    background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%); 
    align-items: center;
    justify-content: center;
    margin-top: -50px;
    margin-bottom: -50px;
}

.contactaddress{
  margin: 50px;
}
.address-col{
  margin-top: 50px;
  margin-bottom: 50px;
  background-color: white;
  border-radius: 10px;
  color: #330c43;
  padding: 10px;
}
.address-col:hover{
  color: #330c43;
  background-color: white;
  transform: scale(1.05);
}
.address-col .icon1{
  color: #330c43;
  font-size: 50px;
}

@media screen and (max-width: 768px) {
  .address-col {
    width: 100%;
  }
  .conatctpara1{
    font-size: 25px;
    margin-bottom: -5px;
  }
  .conatctpara2{
    font-size: 14px;
    padding: 5px;
    padding-top: -20px;
  }
  .icon1{
    padding-top: -10px;
  }
}