.footer-style-1 {
  background-color: #FFF6DA;
  color: Black;
  text-align: left;
  padding: 20px;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.footer-section h3 {
  font-size: 18px;
  margin-bottom: 10px;
}
.footer-section3 h3 {
  font-size: 18px;
  margin-bottom: 10px;
}
.link a {
  text-decoration: none;
  color: black;
}
.footer-section p {
  margin: 5px 0;
  text-decoration: none;
}
.footer-section p:hover{
  transform: scale(1.07);

}
.social-icons {
  display: flex;
  
  margin-top: 10px; 
}

.social-icons a {
  padding-top: 10px;
  padding-right: 10px;
  color: black;
  font-size: 24px;
}
.social-icons a:hover {
  transform: scale(1.07);
}

.divider {
  width: 100%;
  height: 1px;
  background-color: #ddd; 
  margin: 10px 0; 
}


@media screen and (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    text-align: left;
  }

  .footer-section {
    margin: 10px;
  }
  .footer-section3 {
    margin: 10px;
  }
  .footer-section p:hover{
    font-weight: bold;
    transform: scale(0);
  }
  .social-icons {
    flex-wrap: wrap; 
  }

  .find{
    text-align: left;
  }

  .social-icons a {
    text-align: left;
  }
}
