.arrow-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: rgb(231, 176, 253);
  color: rgba(51,12,67,1);
  font-size: 18px;
  padding: 10px;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.arrow-button:hover {
  background-color: rgb(201, 66, 255);
}
