.fullstack-row {
  padding: 20px;
  height: 600px;
}

.fullstack-container {
width: auto;
box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
background-color: rgb(255, 190, 190);
color: rgba(51,12,67,1);
padding: 30px;
border-radius: 20px;
display: flex;
align-items: center;
}
.fullstack-container:hover {
color: rgba(51,12,67,1);
transform: scale(1.02);
background-color: rgb(255, 88, 88);
}
.fullstack-content{
align-items: center;
display: flex;
flex-direction: column;
}
.fullstack-header {
font-size: 28px;
margin-bottom: 10px;
}

.fullstack-text {
font-size: 18px;
padding: 20px;
text-align: justify;
}

.fullstack-image {
width: 100%;
height: auto;
display: block;
margin-top: 50px;
}

.fullstack-image:hover{
transform: scale(1.03);
}

@media (max-width: 768px) {
.fullstack-container {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.fullstack-row {
  height: auto;
}
.fullstack-header{
  font-size: 24px;
  text-align: center;
}
.fullstack-text{
  font-size: 16px;
  padding: 5px;
  line-height: 1.5;
  text-align: justify;
}
.fullstack-image {
  height: 300px;
  margin-top: 0;
  }
}
