.body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #f4f4f4;
  margin-top: -50px;
}

.contact-container {
  margin: 50px;
  width: 85%;
  display: flex;
  align-items: stretch;
  justify-content: center;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.image-box {
  flex: 1;
  overflow: hidden;
}

.image-box img {
  width: 100%;
  height: auto;
  display: block;
}

form {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 60px;
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
  color: white;
  width: 100%;
}

.topic {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: white;
}

.custom-submit-button {
  background-color: #b4cbfd;
  color: rgba(51, 12, 67, 1);
  border: none;
}

.custom-submit-button:hover {
  background-color: white;
  color: #4562a0;
}

.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  padding: 50px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.custom-input,
.custom-textarea {
  width: 100%;
  margin-bottom: 5px;
  font-size: 20px;
}

.custom-textarea .ant-input {
  padding: 0;
}

@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    margin: 10px;
  }
  .box{
    width: 100%;
  }
  .image-box {
    margin-right: 0;
  }
  .topic{
    font-size: 24px;
    text-align: center;
  }
  .custom-input,
.custom-textarea {
  font-size: 16px;
}
  .image-box img {
    order: -1;
  }
  .box{
    padding: 10px;
  }
}
