.founder-container {
  text-align: center;
  padding: 20px;
  height: 100hv;
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
}

.founder-header {
  font-size: 36px;
  margin-bottom: 20px;
  color: white;
}

.card-containerf {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.founder-card {
  width: 250px;
  margin: 20px;
  text-align: center;
}

.founder-image {
  width: 100%;
  border-radius: 50%; 
  background-color: white;
}

.founder-title {
  font-size: 24px;
  margin-bottom: 5px;
  color: purple;
}

.founder-details {
  font-size: 16px;
  color: purple;
}
.founderbody{
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
}
.founderbody:hover{
  background-color: #FFF6DA;
}
@media (max-width: 768px) {
  .founder-card {
    width: 80%;
  }
}
