.extra{
  background: rgb(23,93,105);
  background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
}
.card-container {
  text-align: center;
  margin-bottom: 20px;
  color: #330c43;
  background-color: #eec8fd;
}
.header{
  margin: 20px;
  margin-top: -40px;
  padding-top: 30px;
  color: white;
}
.card-icon{
  font-size: 20px;
  color: #330c43;
}

.card {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  transition: background-color 0.3s ease;
}

.card:hover {
  background-color: #e6f7ff; 
  cursor: pointer;
  transform: scale(1.03);
}

.card h3 {
  margin: 10px 0;
  color: #1890ff; 
}

.card p {
  color: #666;
}

.card-icon {
  font-size: 24px;
  margin-bottom: 10px;
  color: #1890ff; 
}

@media (max-width: 768px) {
  .card-container {
    margin: 25px;
  }
}
