.heading{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 20px;
}

.services-heading {
  font-size: 36px;
  margin-bottom: 10px;
  color: white; 
  margin-top: 0;
}

.services-para {
  font-size: 18px;
  margin-bottom: 20px;
  color: white; 
  max-width: 700px;
}
.homeservice-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.custom-card {
  width: 350px;
  margin: 20px;
  height: auto;
  background: #FFF6DA;
}
.card-body {
  height: 60px; 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px; 
}

.custom-card:hover {
  transform: scale(1.05);
  box-shadow: grey;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.service-title {
  font-size: 18px;
  margin-bottom: 5px;
  margin-top: 0;
  color: #333; 
  align-items: center;
  text-align: center;
}

.buttons {
  background-color: purple;
  border-color: purple;
  color: white;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 5px 120px;
}
.custom-card img {
  margin-bottom: -25px; 
}

.buttons:hover {
  background-color: white;
  color: purple;
}
@media screen and (max-width: 768px) {
  .custom-card {
    height: auto;
  }
  .card-body {
    height: auto; 
  }
  .homeservice-container {
    height: auto;
  }
  .service-title {
    font-size: 16px;
    margin-bottom: 5px;
    margin-top: 0;
  }
  .buttons {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 5px 80px;
  }
}