.contactheader{
  height: 100hv;
}
.contentWrapper {
  display: flex;
  flex-direction: column;
  align-items: center; 
}
.Contactcontent{
  margin-left: 20px;
  padding: 15px;
}
.contacthead {
  color: white;
  font-size: 34px;
}

.contactpara {
  font-size: 18px;
  line-height: 1.5;
  color: white;
  padding-top: 20px;
}

.contactbutton1 {
  background-color: white;
  border: none;
  color: rgb(23, 93, 105);
  font-size: 14px;
  margin-left: -40px;
  margin-top: 20px;
}

.contactbutton1:hover {
  background-color: purple;
  color: white;
  border: none;
}

@media screen and (max-width: 768px) {
  .Contactcontent {
    margin: 20px;
  }

  .contactheader {
    background-image: none;
    display: flex;
    flex-direction: column; 
  }

  .imageWrapper {
    order: 2; 
  }

  .contacthead {
    font-size: 24px;
    text-align: center;
  }

  .contactpara {
    font-size: 16px;
    text-align: justify;
  }
}
