.aboutinfo {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: sticky;
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
  color: white;
}
.infopara{
  margin-top: 50px;
  font-size: 40px;
  color: white;
}
.infopara1{
  padding-top: 10px;
  font-size: 20px;
  color: white;
}
.infopara2{
  padding-top: 20px;
  padding-right: 50px;
  font-size: 16px;
  color: white;
}

.working-image {
  width: 100%; 
  height: auto; 
  max-width: 100%; 
}
.column1 {
  padding-left: 20px;
  text-align: left;
}

@media (max-width: 768px) {
  .aboutinfo {
    height: auto; 
    padding-left: 20px;
    padding-right: 20px;
  }
  .infopara{
    font-size: 26px;
  }
  .infopara1{
    font-size: 20px;
    text-align: justify;
  }
  .infopara2{
    font-size: 16px;
    padding-bottom: 20px;
    padding-right: 20px;
    text-align: justify;
  }
}
