nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #FFF6DA;
  color: black;
  position: sticky;
  top: 0;
  z-index: 1;
  height: 50px;
}
.logo {
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 10px;
  color: white;
  padding-top: 10px;
}
.lg {
  color: rgb(255, 123, 0);
}
nav ul{
  display: flex;
}

nav ul li {
  list-style: none; 
}

nav ul li a{
  display: block;
  text-decoration: none;
  color: black;
  padding: 0.5rem;
  margin: 0 0.5rem;
  border-radius: 0.5rem;
  font-size: 16px;
}

nav ul li a:hover{
  background-color: #F66B34;
}

nav .menu{
  display: none;
  position: absolute;
  top: 0.75rem;
  right: 0.5rem;
  flex-direction: column;
  justify-content: space-between;
  width: 2.25rem;
  height: 2rem;
  cursor: pointer;
}

@media (max-width:768px){
  nav .menu{
      display: flex;
      margin-top: 16px;
      font-size: 30px;
  }

  nav {
      flex-direction: column;
      align-items: flex-start;
      height: auto;
  }

  nav ul{
      display: none;
      flex-direction: column;
      width: 100%;
      margin-bottom: 0.25rem;
      
  }
  nav ul li a:hover{
    margin-left: 50px;
    margin-right: 50px;
  }

  nav ul.open {
      display: flex;
  }

  nav ul li{
      width: 100%;
      text-align: center;
  }

  nav ul li a{
      margin: 0.2rem 0.5rem;
  }
}
