.ecom-row {
  background-color: #f0f0f0;
  background: rgb(23, 93, 105);
  background: linear-gradient(90deg, rgba(23, 93, 105, 1) 0%, rgba(51, 12, 67, 1) 48%);
  padding: 20px;
}

.ecom-container {
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: rgb(252, 244, 178);
  color: rgba(51, 12, 67, 1);
  padding: 30px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.ecom-container:hover {
  color: rgba(51, 12, 67, 1);
  transform: scale(1.02);
  background-color: rgb(252, 235, 88);
}

.ecom-content {
  /* align-items: center; */
  display: flex;
  flex-direction: column;
}

.ecom-header {
  font-size: 28px;
  margin-bottom: 10px;
  padding-left: 120px;
}

.ecom-text {
  font-size: 18px;
  padding: 20px;
  text-align: justify;
}

.ecom-image {
  width: 100%;
  height: auto;
  display: block;
}

.ecom-icon {
  font-size: 24px;
  margin-left: 50px;
  color: rgba(51, 12, 67, 1);
}


.ecom-image:hover {
  transform: scale(1.03);
}





@media (max-width: 768px) {
  .ecom-container {

    margin: 10px;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
  }
  .ecom-image {
    height: 300px;
  }

  .ecom-header {
    padding-left: 0;
    font-size: 24px;
    text-align: center;
  }

  .ecom-text {
    font-size: 16px;
    padding: 5px;
    line-height: 1.5;
    text-align: justify;
  }

  .chatpa {
    font-size: 16px;
  }

  .ecompa {
    font-size: 18px;
  }

  .ecom-point {
    display: flex;
    margin-bottom: 15px;
    /* margin-left: -30px; */
  }

  .mobile-icon {
    font-size: 24px;
    color: rgba(51, 12, 67, 1);
  }

  .ecom-icon {
    font-size: 30px;
    color: rgba(51, 12, 67, 1);
    margin-left: 0;
  }
}