.aboutexp{
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
margin-top: -5px;
}

.custom-col {
  background-color: white; 
  color: purple;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
  margin: 20px; 
}
.custom-col:hover {
  background-color: #FFF6DA; 
}
h1{
  font-size: 45px;
}
h4{
  font-size: 18px;
}
.custom-space {
  width: 100%;
  text-align: center;
  margin-top: 10px; 
}

.icon{
  color: purple;
  font-size: 50px;
}
@keyframes moveUp {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes moveDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}

.custom-col:nth-child(1) {
  animation: moveUp 4s infinite alternate ease-in-out;
}

.custom-col:nth-child(2) {
  animation: moveDown 4s infinite alternate ease-in-out;
}

.custom-col:nth-child(3) {
  animation: moveUp 4s infinite alternate ease-in-out;
}

.custom-col:nth-child(4) {
  animation: moveDown 4s infinite alternate ease-in-out;
}

@media screen and (max-width: 768px) {
  .custom-col {
    width: 80%; 
  }
}
