*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.body5{
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 30px;
}
.container5{
  max-width: 800px;
  width: auto;
  height: 500px;
  border: 7px solid #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  overflow: hidden;
}
.image-box5{
  display: flex;
  height: 100%;
  width: 400%;
  justify-content: space-between;
}
.image-box5{
  animation: imgBox 10s linear infinite;
}
@keyframes imgBox{
  0%{
    margin-left: 0;
  }
  100%{
    margin-left: -300%;
  }
}
.image-box5:hover{
  animation-play-state: paused;
}
.image-box5 .image55{
  width: calc(100% - 100px);
  height: 100%;
}
.image-box5 img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.careers-header {
  color: rgba(51,12,67,1); 
}

.careers-text {
  color: rgba(51,12,67,1);
  font-size: 22px; 
}

.centered-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  padding-top: -20px;
  padding-left: 40px;
  padding-right: 40px;
}
@media (max-width: 768px) {
  .centered-content{
    padding: 10px;
  }
  .body5{
    height: auto;
  }
  .careers-header{
    font-size: 24px;
  }
  .careers-text{
    font-size: 16px;
    padding: 5px;
    text-align: justify;
  }
}


