.about-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100vh; 
}

.cardab {
  width: 600px;
  height: 520px; 
  border-radius: 50px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  background: rgb(23,93,105);
background: linear-gradient(90deg, rgba(23,93,105,1) 0%, rgba(51,12,67,1) 48%);
  color: white;
}

.card img{
  margin-bottom: -10px;
}

.card-image {
  width: 100%;
  height: 70%;
  align-items: center;
  object-fit: cover;
}

.card-content {
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;
  text-align: center;
}

.card-title {
  font-size: 26px;
  margin-bottom: 10px;
}

.card-para {
  font-size: 18px;
  color: white;
}

@media (max-width: 768px) {
  .about-container {
    flex-direction: column;
    align-items: center;
    height: auto;
  }

  .cardab {
    margin-top: 50px;
    width: 80%;
    margin-bottom: 20px;
    padding-bottom: 10px;
    height: auto;
  }

  .card-image{
    height: 0px;
  }
  
  .card-title {
    font-size: 24px;
    margin-bottom: 10px;
  }

  .card-para {
    font-size: 18px;
    color: white;
  }
  .card-content {
    margin-top: 0;
    height: auto;
  }
}