.digital-row {
  padding: 20px;
}

.digital-container {
  width: auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  background-color: rgb(193, 253, 191);
  color: rgba(51, 12, 67, 1);
  padding: 30px;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

.digital-container:hover {
  color: rgba(51, 12, 67, 1);
  transform: scale(1.02);
  background-color: rgb(108, 255, 103);
}

.digital-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.digital-header {
  font-size: 28px;
  margin-bottom: 10px;
}

.digital-text {
  font-size: 18px;
  padding: 20px;
  text-align: justify;
}

.digital-image {
  width: 98%;
  height: auto;
  display: block;
  border-radius: 50px;
}

.digital-image:hover {
  transform: scale(1.03);
}

@media (max-width: 768px) {
  .digital-container {
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .digital-image {
    width: 100%;
  }
  .digital-header{
    font-size: 24px;
    text-align: center;
  }
  .digital-text{
    font-size: 16px;
    line-height: 1.5;
    padding: 5px;
    text-align: justify;
  }
}